import {
  totalLength,
  collectedLength,
  allTotalLength,
  allCollectedLength,
} from "../utils/filterItems";
import { navs } from "../utils/navs";

// 
let abort = false;

addEventListener("message", (e) => {
  const { data } = e;

  if (data.abort) {
    abort = true;
    return;
  } else {
    abort = false;
  }

  /* TotalLength */
  postMessage({
    requestId: data.requestId,
    allTotalLength: allTotalLength({
      isFullMode: data.isFullMode,
      partnerlist: data.partnerlist,
    }),
  });

  /*  */
  if (abort) return;

  /* CollectedLength */
  postMessage({
    requestId: data.requestId,
    allCollectedLength: allCollectedLength({
      collected: data.collected,
      isFullMode: data.isFullMode,
      partnerlist: data.partnerlist,
    }),
  });

  /*  */
  if (abort) return;

  /* navTotalLengthCollectedLength */
  let navsLengths = {};
  navs
    .filter((nav) => {
      return nav.id !== "exchange" && nav.id.indexOf("separator");
    })
    .forEach((nav) => {
      const subnavs = nav.subnavs;
      if (subnavs) {
        subnavs.forEach((subnav) => {
          navsLengths[subnav.id] = [
            totalLength({
              nav: subnav.id,
              isFullMode: data.isFullMode,
              partnerlist: data.partnerlist,
            }),
            collectedLength({
              nav: subnav.id,
              collected: data.collected,
              isFullMode: data.isFullMode,
              partnerlist: data.partnerlist,
            }),
          ];
        });
        /*  */
        if (abort) return;
      } else {
        navsLengths[nav.id] = [
          totalLength({
            nav: nav.id,
            isFullMode: data.isFullMode,
            partnerlist: data.partnerlist,
          }),
          collectedLength({
            nav: nav.id,
            collected: data.collected,
            isFullMode: data.isFullMode,
            partnerlist: data.partnerlist,
          }),
        ];
      }
      /*  */
      if (abort) return;
      /* navAndroidCollectedLength */
      if (Object.keys(navsLengths).length >= 10) {
        postMessage({ requestId: data.requestId, navsLengths: navsLengths });
        navsLengths = {};
      }
    });

  /* nav */
  if (Object.keys(navsLengths).length > 0) {
    postMessage({ requestId: data.requestId, navLengths: navsLengths });
  }

  return postMessage({ requestId: data.requestId, complete: true });
});

export default {};
